#modal {
  position: fixed;
  z-index: 999;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.7);
}

#cookie-modal {
  position: absolute;
  width: 100%;
  height: 100%;
}

#cookiepopup {
  position: relative;
  background-color: #ffffff;
  margin: auto;
  padding: 0;
  width: 350px;
  max-width: 95%;
  z-index: 10000;
  margin: 80px auto;
  text-align: left;
}

#cookiepopup p {
  width: 100%;
  padding: 0.5rem 0;
  line-height: 1.2;
  margin: 0;
  max-width: 100%;
  font-size: 14px;
}

#cookiepopup li {
  font-size: 14px;
}

#cookiepopup a {
  color: #e1171e;
  text-decoration: none;
}

#cookiepopup h4 {
  margin: 0;
  padding: 0;
  color: #e1171e;
  font-size: 14px;
}

.cookiewall {
  width: 90%;
  margin: 0 auto;
}

.cookie-button {
  width: 100%;
  box-sizing: border-box;
  border: 0;
  margin: 0 0 10px;
  padding: 0.5rem 1rem;
  color: #fff;
  font-weight: normal;
  cursor: pointer;
  font-size: 1rem;
}
.cookie-checkbox {
  float: right;
}

#cookiepopup .cookie-logo {
  margin: 0 auto 1rem;
  width: 64px;
  height: 64px;
}
#cookiepopup .cookie-logo img {
  width: 64px;
}

.cookie-additional-info {
  background-color: #efefef;
  padding: 0.5rem;
}

#cookiepopup a.cookie-link,
#cookiepopup a.cookie-link:visited {
  color: #999999;
}

#cookiepopup p.cookielinks {
  padding: 0 0 0.5rem;
  margin: 0;
  height: 1rem;
}

ul.bullets {
  margin-left: 0;
  padding: 0;
}

ul.bullets li {
  list-style-type: disc;
  margin-left: 1.4rem;
}

.grey {
  background-color: #bfbfbf;
}

.red {
  background-color: #e1171e;
}

.cookies-image {
  position: relative;
  height: 0;
  margin: 0 auto;
  width: 100px;
}

.cookies-image img {
  position: relative;
  width: 100px;
  margin: 0 auto;
  top: -75px;
  left: -8px;
}
